<script>
// 重置
const defaultListQuery = {
  pageNum: 1,
  pageSize: 10,
  id: null,
  receiverKeyword: null,
  status: null,
  createTime: null,
  handleMan: null,
  handleTime: null,
};
export default {
  name: "dataList",
  data() {
    return {
      Project: [
        {
          project_id: "888",
          project_name: "大数据杯",
        },
        {
          project_id: "999",
          project_name: "校长杯",
        },
      ],
      tableLabel: [
        { label: "项目ID", prop: "project_id" },
        { label: "项目名字", prop: "project_name" },
      ],
      // 用户id
      UserInfo: {
        id: "",
      },
      select: {
        selected: "",
        selected_value: "",
      },
      add_pro:{
        project_id: "",
        project_name: "",
      },
      check_id: "",
      //搜索选择的类别
      dialogVisible: false,
      pageData: null, //接收html格式代码
      htmlTitle: "添加项目",
      isShow: true,
      isCanvas: false,
    };
  },
  async mounted() {
    // 获取datas
    this.UserInfo.id = this.$route.query.id;
    this.$router.push({ path: '/project:id' ,query: {id:this.UserInfo.id}})
  },
  methods: {
    // 得到数列
    getdata(){
      axios
        .get("http://127.0.0.1:8000/project")
        .then(function(project_id,project_name) {
          console.log(project_id,project_name);
          this.Project.project_id=project_id;
          this.Project.project_name=project_name;
        });
    },
    // 通过关键词搜索待定
    Search() {
      // 向后台发送数据，得到结果
      axios
        .post("http://127.0.0.1:8000/project", {
          type: this.select.selected,
          info: this.select.selected_value,
        })
        .then(function(response) {
          console.log(response.status);
          if (parseInt(response.status) === 0) {
            //传入对应数据
          } else {
            alert("无该信息");
          }
        });
    },
    // 重置
    ResetSearch() {
      this.listQuery = Object.assign({}, defaultListQuery);
    },
    check_info(val1, val2) {
      this.$router.push({
        path: "/pro_cert:id,project_id,project_name",
        query: { id: this.UserInfo.id, project_id: val1, project_name: val2 },
      });
      // ?uid(val);
    },
    // 弹窗关闭
    handleClose() {
      this.dialogVisible = false;
    },
    // 弹窗显示
    preview() {
      this.dialogVisible = true;
    },
    // 添加新项目提交
    sumbit(){
      if(this.add_pro.project_id==""&&this.add_pro.project_name=="")
      {
        alert("项目ID和项目名未填写");
      }
      else{
        if(this.add_pro.project_id==""||this.add_pro.project_name==""){
          if(this.add_pro.project_id==""){
          alert("项目ID未填写");
          }
          else alert("项目名字未填写");
        }
        else{
          // 传送给后端数据
          alert("添加新项目成功");
          this.dialogVisible = false;
          this.add_pro.project_id=="";
          this.add_pro.project_name=="";
        }
      }
    },
    // 删除项目
    delete_pro(project_id){
       axios
        .post("http://127.0.0.1:8000/project", {
          project_id: project_id,
        })
        alert("删除成功");
    }
  },
};
</script>

